import React, { useState } from "react";
import UserInRaid from "./components/UserInRaid";
import { BrowserRouter as Router, useParams } from "react-router-dom";
import Axios from "axios";

function Raid() {
  let { raidId } = useParams();
  const [raid, setRaid] = useState(false);
  if (!raid) {
    Axios.get(`/raid/${raidId}`).then(({ data }) => {
      setRaid(data);
    });
    return <div></div>;
  } else {
    return (
      <div id="Raid">
        <h2>
          {raid.name} du {raid.date.d}/{raid.date.m + 1}/{raid.date.y}{" "}
        </h2>
        <div className="desc">{raid.desc}</div>
        <div className="roster">
          <div className="valid">
    <div className="title">Prsent {raid.roster.accept.length}</div>
            <div className="list">

              {raid
                ? raid.roster.accept.map(({ user, date }) => {
                    return (
                      <UserInRaid
                        raidId={raidId}
                        userId={user}
                        date={date}
                        key={user}
                        typeRoster={"accept"}
                      />
                    );
                  })
                : false}
            </div>
          </div>
          <div className="refuse">
            <div className="title">Absent {raid.roster.refuse.length}</div>
            <div className="list">
              {raid
                ? raid.roster.refuse.map(({ user, date }) => {
                    return (
                      <UserInRaid
                        raidId={raidId}
                        userId={user}
                        date={date}
                        key={user}
                        typeRoster={"refuse"}
                      />
                    );
                  })
                : false}
            </div>
          </div>
          {/* <div className="bench">
            <div className="title">Bench {raid.roster.bench.length}</div>
            <div className="list">
              {raid
                ? raid.roster.bench.map(({ user, date }) => {
                    // console.log(user, date);
                    return (
                      <UserInRaid
                        raidId={raidId}
                        userId={user}
                        date={date}
                        key={user}
                        typeRoster={"bench"}
                      />
                    );
                  })
                : false}
            </div> 
          </div>*/}
        </div>
      </div>
    );
  }
}

export default Raid;



