import React from "react";
import "./stylesheets/main.css";
import { useCookies } from "react-cookie";
import axios from "axios";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import Calendar from "./calendar";
import Login from "./Login";
import MyCharacter from "./MyCharacter";
import Raid from "./Raid";
import Admin from "./Admin";

function App() {
  const [cookies, setCookie, removeCookie] = useCookies();

  if (cookies.token) {
    // axios.post("/auth/verif", { token: cookies.token }).then(({ data }) => {
    //   if (!data) {
    //     removeCookie("token");
    //     // window.location.reload();
    //   }
    // });
    return (
      <Router>
        <div className="App">
          <div id="nav">
            <ul>
              <li>
                <Link to="/calendar">Calendrier</Link>
              </li>
              <li>Roster</li>

              <li>
                <Link to="/mycharater">Mon Personnage</Link>
              </li>
              <li>WishList</li>
              {cookies.grade == 9 ? (
                <li>
                  <Link to="/admin">Admin</Link>
                </li>
              ) : (
                false
              )}
            </ul>
            <div
              className="profil"
              onClick={() => {
                console.log("DECO");
                removeCookie("token");
                // window.location.reload();
              }}
            >
              profil
            </div>
          </div>
          <div id="main">
            <Switch>
              <Route path="/calendar">
                <Calendar />
              </Route>
              <Route path="/mycharater">
                <MyCharacter />
                {/* LOL */}
              </Route>
              <Route exact={true} path="/">
                Bienvenue les Loulous le site est en construction mais vous
                pouvez dj y particip.
              </Route>
              <Route path="/roster/:raidId">
                <Raid />
              </Route>
              <Route path="/admin">
                <Admin />
              </Route>
            </Switch>
          </div>
        </div>
      </Router>
    );
  } else {
    return <Login />;
  }
}

export default App;



