import React, { useState } from "react";
import Axios from "axios";
import { useCookies } from "react-cookie";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faCheck,
  faTimes,
  faToiletPaper
} from "@fortawesome/free-solid-svg-icons";

function UserInRaid({ userId, raidId, typeRoster }) {
  const [cookies, setCookie, removeCookie] = useCookies();

  const [user, setUer] = useState(false);
  if (!user) {
    Axios.get(`/api/user/id/${userId}`).then(({ data }) => {
      setUer(data);
    });
    return <div></div>;
  } else {
    return (
      <div className="Membre">
        <div
          className={`iconClass ${user.classe}`}
          style={{ height: "20px", width: "20px" }}
        ></div>
        <div
          className={`iconSpe ${user.spe}`}
          // style={{ height: "20px", width: "20px" }}
        ></div>
        <div className={`name colorClass ${user.classe}`}>{user.name}</div>
        {cookies.grade == 9 ? (
          <div className="tools">
            {typeRoster !== "accept" ? (
              <div className="accept">
                <FontAwesomeIcon icon={faCheck} />
              </div>
            ) : (
              false
            )}
            {typeRoster !== "refuse" ? (
              <div className="refuse">
                <FontAwesomeIcon icon={faTimes} />
              </div>
            ) : (
              false
            )}
            {/* {typeRoster !== "bench" ? (
              <div className="bench">
                <FontAwesomeIcon icon={faToiletPaper} />
              </div>
            ) : (
              false
            )} */}
          </div>
        ) : (
          false
        )}
      </div>
    );
  }
}

export default UserInRaid;



