import React, { useState, useRef } from "react";
import { useCookies } from "react-cookie";
import Axios from "axios";

const Classes = {
  druid: {
    color: "#FF7D0A",
    name: "Druide"
  },
  hunter: {
    color: "#ABD473",
    name: "Chasseur"
  },
  mage: {
    color: "#69CCF0",
    name: "Mage"
  },
  paladin: {
    color: "#F58CBA",
    name: "Paladin"
  },
  priest: {
    color: "#FFFFFF",
    name: "Pretre"
  },
  rogue: {
    color: "#FFF569",
    name: "Voleur"
  },
  shaman: {
    color: "#0070DE",
    name: "Chaman"
  },
  warlock: {
    color: "#9482C9",
    name: "Dmoniste"
  },
  warrior: {
    color: "#C79C6E",
    name: "Guerrier"
  }
};
console.log(Classes);

// import { Stuff } from "../../database";

function MyCharacter() {
  const [cookies, setCookie, removeCookie] = useCookies();
  // console.log(cookies.user);
  const [myCharacter, setMyCharacter] = useState(false);
  const selectClasse = useRef();
  if (!myCharacter) {
    Axios.get(`/api/user/id/${cookies.user}`).then(({ data }) => {
      setMyCharacter(data);
    });
  }
  const Stuff = myCharacter
    ? myCharacter.stuff.map((piece, index) => {
        // console.log(piece.type);
        return (
          <a href="#" data-wowhead={`item=${piece.item}`} key={index}>
            <div className={piece.type}>{piece.item}</div>
          </a>
        );
      })
    : false;
  return (
    <div id="MyCharacter" className={myCharacter.classe}>
      <div className="name">
        <div className={`iconClass ${myCharacter.classe}`}></div>
        <div className="text">{myCharacter.name}</div>
      </div>
      <div className="selectClasse">
        <select
          name="selectClasse"
          ref={selectClasse}
          defaultValue={myCharacter.classe}
          onChange={() => {
            // console.log(selectClasse.current.value);
            myCharacter.classe = selectClasse.current.value;
            // setMyCharacter(myCharacter)
            // console.log(myCharacter);
            Axios.post("/api/user/update/", { myCharacter }).then(
              ({ data }) => {
                console.log(data);
                if(data){
                  setMyCharacter(false)
                }
              }
            );
          }}
        >
          {Object.keys(Classes).map(index => {
            return (
              <option value={index} key={index}>
                {Classes[index].name}
              </option>
            );
          })}
        </select>
      </div>
      {/* <div className="spe">{myCharacter.spe}</div> */}
      {/* <div className="stuff">{Stuff}</div> */}
      {/* <div className="wishlist">WishList</div> */}

      {/* <a href="#" data-wowhead="item=18490">
        hai
      </a> */}
    </div>
  );
}

export default MyCharacter;



