import React, { useState } from "react";
import { useCookies } from "react-cookie";
import Axios from "axios";

import SelectClasse from "./listDeroulante/SelecteClasse";
import SelectGrade from "./listDeroulante/Grade";
import SelectSpec from "./listDeroulante/Spec";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTrash} from "@fortawesome/free-solid-svg-icons";

function Admin() {
  const [cookies, setCookie, removeCookie] = useCookies();
  const [Users, setUsers] = useState(false);
  if (Users == false) {
    Axios.get("/api/users").then(({ data }) => {
      setUsers(data);
    });
  }
  if (cookies.grade == 9) {
    return (
      <div id="Admin">
        <h3>Roster</h3>

        <div className="roster">
          {Users
            ? Users.map(user => {
                // console.log(user);
                return (
                  <div className="user" key={user.id}>
                    <div className={`classe iconClass ${user.classe}`}></div>
                    <div className={`spe iconSpe ${user.spe}`}></div>
                    <div className={`name colorClass ${user.classe}`}>
                      {user.name}
                    </div>
                    <div className="SelectClasse">
                      <SelectClasse user={user} callback={setUsers} />
                    </div>
                    <div className="SelectGrade">
                      <SelectGrade user={user} callback={setUsers} />
                    </div>
                    <div className="SelectSpec">
                        <SelectSpec user={user} callback={setUsers} /> 
                    </div>
                    <div className="countAccept">0</div>
                    <div className="countRefuse">0</div>
                    <div className="countBench">0</div>
                    <div
                      className="delete"
                      onClick={() => {
                        Axios.get(`/api/user/delete/${user.id}`).then(
                          ({ data }) => {
                            if (data) {
                              setUsers(false);
                            }
                          }
                        );
                      }}
                    >
                      <FontAwesomeIcon icon={faTrash} />
                    </div>
                  </div>
                );
              })
            : false}
        </div>
      </div>
    );
  } else {
    return <div>Petit Malin va :D</div>;
  }
}

export default Admin;



